<template>
  <div id="app">
    <el-container>
        <el-header>TestDapp</el-header>
        <el-main>

          <!-- 钱包信息 -->
           <el-row>
            <el-col :span="6">
              <div class="connect-btn-bg">
                Wallet Address:
              </div>
            </el-col>
            <div class="connect-btn-bg">
              {{ getWalletAddress }}
            </div>
           </el-row>


          <el-row>
            <el-col :span="24">
              <div class="connect-btn-bg">
                Universal Link: {{ updateUniLink }}
              </div>
            </el-col>
          </el-row>

          <!-- 连接钱包 -->
          <el-row :gutter="12">
            <el-col :span="12">
              <div class="connect-btn-bg">
                <el-input v-model="testProofPayload" placeholder="proofPayload"></el-input>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="connect-container">
                <el-button type="primary" v-on:click="connectBtnClicked" icon="el-icon-share" :disabled="this.testProofPayload.length == 0">{{ connectStatusStr }}</el-button>
              </div>
            </el-col>
          </el-row>

          <!-- 本地加解密KeyPair -->
          <el-row :gutter='12'>
            <el-col :span="12">
              <div class="connect-btn-bg">
                PubKey: <el-input v-model="localPubKey" placeholder="PublicKey" disabled></el-input>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="connect-btn-bg">
                SecKey: <el-input v-model="localSecKey" placeholder="SecretKey" disabled></el-input>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="18">
              <div class="connect-btn-bg">
                <el-input v-model="testTransaction" placeholder="sendTransactions"></el-input>
              </div>
            </el-col>
            <el-col :span="6">
              <div class="connect-container">
                <el-button type="primary" v-on:click="sendTransaction()">{{ testTransactionStr }}</el-button>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <div class="connect-container">
                <el-button type="primary" v-on:click="clearCacheKeyPair()" icon="el-icon-delete">清理本地缓存</el-button>
              </div>
            </el-col>
          </el-row>
        </el-main>
    </el-container>

  </div>
</template>

<script>
function testEncrypt(msg) {
  console.log('------------------------------')
  var clientKeyPairPub = "5a3056411e213701efaae6069412f6c8ebe3b1dcb10b73972d207928b9158e52"
  let client_kp = OKWalletConnectSDK.utils.hexToByteArray(clientKeyPairPub)
  let self_kp = OKWalletConnectSDK.testCrypto.keyPair
  console.log(self_kp)
  console.log(client_kp)

  let res = OKWalletConnectSDK.testCrypto.encrypt(msg, client_kp)
  console.log(OKWalletConnectSDK.utils.toHexString(res))


  let res_back = OKWalletConnectSDK.testCrypto.decrypt(res, client_kp)
  console.log(res_back)
  console.log('------------------------------')
}
async function testconnect() {
  await OKWalletConnectSDK.testCM.connect()
}


async function testSendTransaction(){
  await OKWalletConnectSDK.TON.connect()
  let transactionRequest = {"validUntil":1722481054071,"network":-239,"from":"12345","messages":[{"address":"EQARULUYsmJq1RiZ-YiH-IJLcAZUVkVff-KBPwEmmaQGH6aC","amount":"285000000","payload":"te6cckEBAgEAiAABbQ+KfqUAAACYOR4QzEBfXhAIAO87mQKicbKgHIk4pSPP4k5xhHqutqYgAB7USnesDnCcECwbgQMBAJclk4VhgBD3JEg1TUr75iTijBghOKm/sxNDXUBl7CD6WMut0Q85yAZ+V1MAKNBrbGziXTXC9CetaXjoHL28y7L1wbJwuhzumC3yzL7QVwlJkA=="}]}
  OKWalletConnectSDK.TON.sendTransaction(transactionRequest)
}

export default {
  name: 'App',
  data: ()=> {
    return {
      testTopic: '',
      testPublicKey: '',
      testProofPayload: '',
      pendingDeepLink: '-',
      logList: [],
      connectReady: false,
      connectStatusStr: '连接钱包',
      localPubKey: '',
      localSecKey: '',
      testTransaction:'',
      testTransactionStr:'发送交易',
      walletAddress:'',
    }
  },
  computed: {
    updateUniLink: function() {
      console.log(`update uni link: ${this.testTopic} ${this.testPublicKey}`)
      this.pendingDeepLink = OKWalletConnectSDK.utils.generateDeepLink(
        {
          protocolVer: "1",
          channelId: this.testTopic,
          clientId: this.testPublicKey,
          dAppInfo: {
            name: "test",
            url: "-",
            iconUrl: "-"
          },
          requestId:"0",
          testPayload: this.testProofPayload
        }
      )
      return this.pendingDeepLink
    },
    getWalletAddress:function (){
      return this.walletAddress;
    },


  },
  methods: {
    async connectBtnClicked() {
      console.log('get connect wallet btn clicked');
      this.testTonSdk();
    },

    clearCacheKeyPair: () => {
      console.log(`generate local key pair`)
      OKWalletConnectSDK.testCM.cleanCache()
      // testTonSdk()

    },
    sendTransaction:()=>{
      console.log('sendTransaction')
      testSendTransaction()
    },

    async testTonSdk(){
      console.log('---testProofPayload--->>>>----',this.testProofPayload);
      let proof = {
        tonProof:this.testProofPayload,
        redirect:"tg://resolve"
      };
      console.log('-------------testTonSdk------>>>>----',proof);

      await  OKWalletConnectSDK.TON.connect(proof);


    }

  },
  created() {
    // testEncrypt('test msg')
    console.log(OKWalletConnectSDK.testCrypto)
    console.log('test sdk here')
    console.log(OKWalletConnectSDK.TON)
  },
  mounted() {
    this.connectReady = true
    console.log('test connect called')
    testconnect()
    const keyPairInfo = OKWalletConnectSDK.testCM.getCryptoInfo()
    console.log(keyPairInfo)
    this.localPubKey = keyPairInfo.kp.publicKey
    this.localSecKey = keyPairInfo.kp.secretKey
    this.testPublicKey = this.localPubKey
    this.testTopic = keyPairInfo.topic.content
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
}
</style>



